<template>
<div class="news-item-wrapper">
    <div class="row m-0 header">
        <span class="name">{{title}}</span>
        <span class="more"></span>
    </div>
    <div class="row m-0 notices">
        <span class="notice" v-for="(notice, index) in lists" :key="index" :ref="`notice-${index}`">
            <span class="content" :ref="`content-${index}`">{{notice.breif}}</span>
            <span class="three-dot" :ref="`dot-${index}`"> ...</span> <span class="go-arrow"></span>
        </span>
    </div>
</div>
</template>

<script>
export default {
    name: 'NewsItem',
    props: {
        title: {
            type: String,
            required: true,
        },
        lists: {
            type: Array,
            default: () => [{
                    breif: 'ed sapien interdum.',
                    id: 1
                },
                {
                    breif: 'Quisque sodales nisl.',
                    id: 1
                },
                {
                    breif: 'Quisque sodales nisl at tellus mattis. Cras auctor purus sed sapien interdum.',
                    id: 1
                },
                {
                    breif: 'Quisque sodales nisl at tellus mattis. Cras auctor purus sed sapien interdum.',
                    id: 1
                },
                {
                    breif: 'Quisque sodales nisl at tellus mattis. Cras auctor purus sed sapien interdum.',
                    id: 1
                },
            ]
        },
    },
    mounted: function () {
        this.checkAllNotice();
    },
    methods: {
        checkAllNotice() {
            let refNames = Object.keys(this.$refs);
            let refContent = refNames.filter(ref => ref.includes("content-"));
            for (let cnt of refContent) {
                let index = cnt.split('-')[1];
                console.log(12, this.$refs[cnt][0], this.$refs[cnt][0].clientWidth)
                console.log(555, this.$refs[`notice-${index}`][0], this.$refs[`notice-${index}`][0].clientWidth)
                if (this.$refs[cnt][0].clientWidth < this.$refs[`notice-${index}`][0].clientWidth - 30) {
                    this.$refs[`dot-${index}`][0].style.display = 'none';
                }
            }
        },
    }
}
</script>

