<template>
  <div class="mobile-page home-container">
      <MainLayout :title="$t('Home.Title')" :leftIcon="`BAR`" :leftAction="openSidebar" isHomeBar>
        <Home></Home>
      </MainLayout>
      <div class="sidebar-overlay" :class="isOpen ? 'showIn' : 'showOut'" @click="closeSidebar"></div>
      <Sidebar />
  </div>
</template>

<script>

import MainLayout from '@/components/Common/Layout/MainLayout';
import Sidebar from "@/components/Common/Layout/Sidebar";
import Home from '@/components/Home/Home';

export default {
  name: 'HomeContainer',
  components: {
    MainLayout,
    Sidebar,
    Home,
  },
  data: function () {
    return {
      isOpen: false,
    }
  },
  mounted: function () {
    this.$root.$on("sidebar:close", () => {
      this.isOpen = false;
    });
  },
  methods: {
    openSidebar: function () {
      this.isOpen = true;
      this.$root.$emit("sidebar:open");
    },
    closeSidebar: function () {
      this.isOpen = false;
      this.$root.$emit("sidebar:close");
    }
  }
}
</script>

<style lang="scss">
  @import '@/assets/styles/variables.scss';
  @import '@/assets/styles/home.scss';
</style>
