<template>
<div class="sidebar-wrapper" :class="isOpen ? 'fadeIn' : 'fadeOut'">
    <!-- <div class="sidebar-overlay" :class="isOpen ? 'showIn' : 'showOut'" @click="handleCloseSidebar"></div> -->
    <div class="sidebar-nav" :class="isOpen ? 'slideInLeft' : 'slideOutLeft'">
        <div class="sidebar-item top">
            <span class="sidebar-logo"></span>
            <span class="sidebar-close" @click="handleCloseSidebar"></span>
        </div>
        <div class="sidebar-item level-1" @click="gotoPage('home')" :class="`${$route.path === '/home' ? 'active' : '' }`">
            <span class="icon home">{{$t('Sidebar.Home')}}</span>
        </div>
        <div class="sidebar-item level-1" @click="gotoPage('projects')" :class="`${$route.path === '/projects' ? 'active' : '' }`">
            <span class="icon project">{{$t('Sidebar.ProjectList')}}</span>
        </div>
        <div class="sidebar-item level-1" :class="`${$route.path.startsWith('/my-page') ? 'active' : '' }`">
            <span class="icon user">{{$t('Sidebar.MyPage')}}</span>
        </div>
        <div class="sidebar-item level-2" @click="gotoPage('my-page/career')" :class="`${$route.path.startsWith('/my-page/career') ? 'active' : '' }`">
            <span class="icon dot">{{$t('Sidebar.MyCareer')}}</span>
        </div>
        <div class="sidebar-item level-2" @click="gotoPage('my-page/apply-list')" :class="`${$route.path.startsWith('/my-page/apply-list') ? 'active' : '' }`">
            <span class="icon dot">{{$t('Sidebar.ApplyList')}}</span>
        </div>
        <div class="sidebar-item level-2 last" @click="gotoPage('my-page/contact-list')" :class="`${$route.path.startsWith('/my-page/contact-list') ? 'active' : '' }`">
            <span class="icon dot">{{$t('Sidebar.ContactList')}}</span>
        </div>
        <div class="sidebar-item level-1" @click="gotoPage('notices')" :class="`${$route.path === '/notices' ? 'active' : '' }`">
            <span class="icon bell">{{$t('Sidebar.Notice')}}</span>
        </div>
        <div class="sidebar-item level-1" @click="gotoPage('cynews')" :class="`${$route.path === '/cynews' ? 'active' : '' }`">
            <span class="icon rss">{{$t('Sidebar.CYNews')}}</span>
        </div>
        <div class="sidebar-item level-1" @click="gotoPage('partner-news')" :class="`${$route.path === '/partner-news' ? 'active' : '' }`">
            <span class="icon group">{{$t('Sidebar.PartnerNews')}}</span>
        </div>
        <div class="sidebar-item level-1" @click="gotoPage('settings')" :class="`${$route.path === '/settings' ? 'active' : '' }`">
            <span class="icon setting">{{$t('Sidebar.Setting')}}</span>
        </div>
        <div class="sidebar-item level-1" @click="doLogout">
            <span class="icon logout">{{$t('Sidebar.Logout')}}</span>
        </div>
    </div>
</div>
</template>

<script>
import {
    LOGOUT
} from '@/mutations/Auth.mutation';
export default {
    name: "Sidebar",
    data: function () {
        return {
            isOpen: false,
        }
    },
    mounted: function () {
        this.$root.$on("sidebar:open", this.handleOpenSidebar);
        this.$root.$on("sidebar:close", () => {
            this.isOpen = false;
        });
    },
    methods: {
        handleOpenSidebar() {
            this.isOpen = true;
        },
        handleCloseSidebar() {
            this.isOpen = false;
            this.$root.$emit("sidebar:close");
        },
        doLogout() {
            this.$store.dispatch(LOGOUT);
        },
        gotoPage(name, props) {
            if (props) {
                if (name == 'my-page') {
                    return this.$router.push({
                        name: 'MyPageContainer',
                        params: {
                            tab: props
                        }
                    });
                }
            } else if (name == 'home') {
                this.handleCloseSidebar();
            } else {
                this.$router.push(name);
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/variables.scss';

.sidebar-wrapper {
    display: inline-flex;
    height: 100%;
    width: 280px;
    z-index: 20;
    position: fixed;

    .sidebar-overlay {
        display: inline-flex;
        height: 100%;
        width: 100%;
        background: #333333;
        opacity: 0.3;
        z-index: 21;
        position: fixed;
    }

    .sidebar-nav {
        display: inline-flex;
        height: 100%;
        width: 100%;
        background: $cy-white;
        z-index: 12;
        position: fixed;
        flex-flow: column;

        .level-1,
        .level-2 {
            border-bottom: none;
        }

        .level-2+.level-1 {
            border-top: 0.5px solid #E0E0E0;
        }

        .level-2.last {
            border-bottom: 0;
        }

        .sidebar-item {
            display: inline-flex;
            padding-top: 12px;
            padding-bottom: 12px;
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: normal;
            font-size: 15px;
            line-height: 22px;
            color: #333333;

            &.top {
                padding: 12px 15px;
            }

            &.level-1 {
                margin-left: 20px;
                width: calc(100% - 20px);
            }

            &.level-2 {
                margin-left: 50px;
                width: calc(100% - 50px);
            }

            width: 100%;
            height: fit-content;
            align-items: center;
            justify-content: space-between;
            border-bottom: 0.5px solid #E0E0E0;

            .sidebar-logo {
                background-image: url(~@/assets/images/cy-logo@3x.png);
                width: 190px;
                height: 56px;
                background-size: contain;
                background-repeat: no-repeat;
                background-position: center;
            }

            .sidebar-close {
                background-image: url(~@/assets/images/close.png);
                width: 16px;
                height: 16px;
                background-size: contain;
                background-position: center;
                background-repeat: no-repeat;
                margin-right: 5px;
            }

            .icon {
                display: inline-block;
                padding-left: 32px;
                position: relative;

                &.dot {
                    padding-left: 17px;
                }

                &:after {
                    content: '';
                    height: 22px;
                    width: 22px;
                    display: inline-block;
                    top: 0;
                    left: 0;
                    position: absolute;
                    background-size: contain;
                    background-position: center;
                    background-repeat: no-repeat;
                }

                &.home:after {
                    background-image: url(~@/assets/images/home.png);
                }

                &.project:after {
                    background-image: url(~@/assets/images/project.png);
                }

                &.user:after {
                    background-image: url(~@/assets/images/user.png);
                }

                &.dot:after {
                    background-image: url(~@/assets/images/dot.png);
                    background-position: 0 50%;
                    background-size: 6px 6px;
                }

                &.bell:after {
                    background-image: url(~@/assets/images/bellb.png);
                }

                &.rss:after {
                    background-image: url(~@/assets/images/rss.png);
                }

                &.group:after {
                    background-image: url(~@/assets/images/group.png);
                }

                &.setting:after {
                    background-image: url(~@/assets/images/setting.png);
                }

                &.logout:after {
                    background-image: url(~@/assets/images/logout.svg);
                }
            }

            &.active .icon {
                color: $cy-blue;

                &.home:after {
                    background-image: url(~@/assets/images/home-active.png);
                }

                &.project:after {
                    background-image: url(~@/assets/images/project-active.png);
                }

                &.user:after {
                    background-image: url(~@/assets/images/user-active.png);
                }

                &.dot:after {
                    background-image: url(~@/assets/images/dot.png);
                    background-position: 0 50%;
                    background-size: 6px 6px;
                }

                &.bell:after {
                    background-image: url(~@/assets/images/bellb-active.png);
                }

                &.rss:after {
                    background-image: url(~@/assets/images/rss-active.png);
                }

                &.group:after {
                    background-image: url(~@/assets/images/group-active.png);
                }

                &.setting:after {
                    background-image: url(~@/assets/images/settings-active.png);
                }

                &.logout:after {
                    background-image: url(~@/assets/images/logout.svg);
                }
            }
        }
    }
}

.fadeIn {
    //animation: fadeIn 0.3s;
    transform: translate3d(0, 0, 0);
    transition: all 0.3s
}

.fadeOut {
    // animation: fadeOut 0.3s;
    transform: translate3d(-100%, 0, 0);
    transition: all 0.3s
}

.showIn {
    //animation: fadeIn 0.3s;
    transform: translate3d(0, 0, 0);
    transition: all 0s
}

.showOut {
    // animation: fadeOut 0.3s;
    transform: translate3d(-100%, 0, 0);
    transition: all 0s
}
</style>
