<template>
<div class="project-item-wrapper" v-if="project" @click="gotoProjectDetail">
    <div class="project-title">
        <span class="project-status" :class="project.status == complete ? 'complete' : ''">{{project.projstatus ? project.projstatus : ''}}</span><span class="project-name">{{project.projNm ? project.projNm : '---'}}</span>
    </div>
    <div class="project-info">
        <div class="d-flex mt-8px">
            <span class="info duration">{{project.projFrom | dateFormat}} ~ {{project.projTo | dateFormat}}</span>
            <span class="info user">{{project.applyCnt ? project.applyCnt : ''}}</span>
        </div>
        <div class="d-flex">
            <span class="info location">{{project.location1 ? project.location1 : ''}} {{project.location2 ? project.location2 : ''}} {{project.location3 ? project.location3 : ''}}</span>
        </div>
        <div class="d-flex mt-8px" v-if="project.keyword && project.keyword.length">
            <span class="info tags">
                <span class="tag" v-for="(tag, index) in project.keyword.split(',')" :key="index">{{tag}}</span>
            </span>
        </div>
    </div>
</div>
</template>

<script>
import moment from 'moment'
import {
    PROJECT_STATUS,
    CURRENT_STATUS
} from '@/constants'
export default {
    name: "ProjectItem",
    props: {
        project: null
    },
    data: function () {
        return {
            complete: PROJECT_STATUS.COMPLETE,
            process: PROJECT_STATUS.PROCESS
        }
    },
    methods: {
        gotoProjectDetail() {
            if (this.project.projCd) {
                this.$router.push(`project/${this.project.projCd}`)
            }
        }
    },
    created() {
        if (this.project && this.project.status) {
            switch (this.project.status) {
                case PROJECT_STATUS.PROCESS:
                    this.project.projstatus = 'Process'
                    break;
                case PROJECT_STATUS.COMPLETE:
                    this.project.projstatus = 'Complete'
                    break;
                default:
                    break;
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.mb-8px {
    margin-bottom: 8px !important;
}

.mt-8px {
    margin-top: 8px !important;
}

.project-item-wrapper~.project-item-wrapper {
    margin-top: 1.5rem;
}

.project-item-wrapper {
    background: #FFFFFF;
    border-radius: .6rem;
    padding: 1.5rem;

    .project-title {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: 2.3rem;
        display: flex;
        align-items: center;
        color: #333333;

        .project-status {
            background: #006FE3;
            border-radius: 2.0rem;
            padding-left: .7rem;
            padding-right: .7rem;
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: normal;
            font-size: 1.2rem;
            line-height: 2.0rem;
            align-items: center;
            color: #FFFFFF;
            margin-right: 1.0rem;
            display: inline-flex;

            &.complete {
                background-color: #27AE60;
            }
        }
    }

    .project-info {
        .info {
            padding-left: 2.5rem;
            position: relative;
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: 300;
            font-size: 1.5rem;
            line-height: 3.2rem;
            min-height: 3.2rem;
            color: #333333;

            &.duration {
                width: 23.0rem;
            }

            &.tags {
                display: inline-flex;
                flex-flow: wrap;

                .tag {
                    background: rgba(38, 153, 251, 0.1);
                    border-radius: 2.0rem;
                    font-family: NotoSansKR;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 1.2rem;
                    line-height: 1.7rem;
                    display: flex;
                    align-items: center;
                    text-align: center;
                    color: #006FE3;
                    padding-left: .7rem;
                    padding-right: .7rem;
                    margin-right: 1.0rem;
                    height: 2.0rem;
                    top: 0.6rem;
                    position: relative;
                }
            }

            &:after {
                content: '';
                width: 1.6rem;
                height: 1.6rem;
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                background-size: contain;
                background-repeat: no-repeat;
                background-position: center;
            }

            &.duration:after {
                background-image: url('~@/assets/images/calendar-h.png');
            }

            &.user:after {
                background-image: url('~@/assets/images/user-h.png');
            }

            &.location:after {
                background-image: url('~@/assets/images/location-h.png');
            }

            &.tags:after {
                background-image: url('~@/assets/images/tag-h.png');
            }
        }
    }
}
</style>
